}

p a {
  text-decoration: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}

p {
  line-height: 1.4em;
}

em {
  opacity: 0.7;
  font-style: normal;
}

h1 span {
  font-weight: lighter;
  opacity: 0.6;
