/**************** Page switcher ****************/

.page-switcher {
  background: rgba(0, 0, 0, 0.2);
  color: rgba(255, 255, 255, 0.5);
  text-decoration: none;
  transition: all 0.25s;
  position: fixed;
  top: 50%;
  height: 100px;
  width: 100px;
  margin-top: -50px;
  font-size: 40px;
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: center;
}

.page-switcher:hover {
  background: rgba(0, 0, 0, 0.4);
}

.page-switcher-prev {
  left: 0;
}

.page-switcher-prev:hover {
  left: -10px;
}

.page-switcher-next {
  right: 0;
}

.page-switcher-next:hover {
  right: -10px;
}

/**************** JavaScript API examples ****************/

#console {
  height: 100px;
  width: 100%;
  overflow-x: hidden;
  overflow-y: scroll;
  margin: 0 0 30px 0;
}

/******************** Example window ********************/

.example-window {
  text-align: center;
  color: #e9e9e9;
  max-width: 40em;
  display: inline-block;
}

.example-window h1 {
  margin: 0 0 0.5em 0;
}

.example-window p {
  margin: 0 0 1em 0;
}

.example-window button {
  margin: 0;
  padding: 0.5em;
  font-size: 1em;
}

/******************** Forms ********************/

.form-controls {
  margin: 40px;
}

.form-controls label {
  display: block;
  text-align: center;
  opacity: 0.5;
  margin-bottom: 10px;
}

.btn-group {
  margin-bottom: 10px;
}

.btn,
.btn:visited {
  display: inline-block;
  background: #fff;
  color: #000;
  border: 0;
  padding: 0.5em 1.4em;
  text-decoration: none;
  border-radius: 0.2em;
  transition: all 0.25s;
  margin: 0 0.25em;
}

.btn:hover {
  background: #ddd;
  color: #000;
}

/******************** Example selector ********************/

ul {
  list-style: none;
  padding: 0;
}

.example-selector > li {
  display: inline-block;
  height: 6em;
  width: 6em;
  color: rgba(0, 0, 0, 0.7);
  background: rgba(255, 255, 255, 0.7);
  text-align: center;
  cursor: pointer;
  margin: 0.5em;
  padding: 0.5em;
  overflow: hidden;
  transition: all 0.25s;
  border-radius: 5px;
}

.example-selector > li:hover {
  color: rgba(0, 0, 0, 1);
  background: rgba(255, 255, 255, 0.9);
}

.example-selector > li:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  height: 100%;
}

.example-selector > li > span {
  display: inline-block;
  vertical-align: middle;
  padding: 3px 5px;
}

/******************** SelectBox ********************/

.selectbox {
  text-align: center;
  font-size: inherit;
  max-width: 20em;
  padding: 0.2em 0.4em;
}

/******************** Layout ********************/

.center-outer {
  display: table;
}

.center-inner {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
}

.fit {
  width: 100%;
  height: 100%;
}

/******************** Code ********************/

#options {
  display: none;
}

/******************** Responsiveness ********************/

@media (min-width: 820px) and (max-width: 1000px) {
  .example-window {
    margin: 0 100px;
  }
}

@media (max-width: 819px) {
  body {
    padding: 20px;
  }

  .example-window {
    margin: 0;
  }

  .page-switcher {
    position: inherit;
    display: inline-block;
    padding: 10px 10px;
    width: 30px;
    height: 30px;
    font-size: 20px;
    border-radius: 50%;
    margin: 20px 0;
  }
}

/* ========================= */
//! Style the tab
.tab {
  overflow: hidden;
  border: 1px solid #ccc;
  background-color: #f1f1f1;
}

//! Style the buttons that are used to open the tab content
.tab button {
  background-color: inherit;
  float: left;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 14px 16px;
  transition: 0.3s;
}

//! Change background color of buttons on hover
.tab button:hover {
  background-color: #ddd;
}

//! Create an active/current tablink class
.tab button.active {
  background-color: #ccc;
}

//! Style the tab content
.tabcontent {
  display: none;
  padding: 6px 12px;
  border: 1px solid #ccc;
  border-top: none;
}

.cc-type-info,
.cc-type-opt-in {
  background-color: #052d61;
}

